import routesComponent from './routesComponent'

export default [

  {
    //登陆
    path: '/Land',
    name: 'Land',
    component: routesComponent.LandComponent
  },
  {
    //注册
    path: '/register',
    name: 'register',
    component: routesComponent.registerComponent
  },
  {
    //首页
    path: '/index',
    name: 'index',
    component: routesComponent.indexComponent,

    // 二级
    children: [
      {
        //首页
        path: '/home',
        name: 'home',
        component: routesComponent.homeComponent
      },
      {
        //电影
        path: '/chat',
        name: 'chat',
        component: routesComponent.chatComponent
      },
      {
        //论坛
        path: '/live',
        name: 'live',
        component: routesComponent.liveComponent
      },
      {
        //购物车
        path: '/manage',
        name: 'manage',
        component: routesComponent.manageComponent
      },
      {
        //我的
        path: '/shopping',
        name: 'shopping',
        component: routesComponent.shoppingComponent
      },
    ],

  },
  {
    // 详情页
    path: '/movieDetail/:id',
    name: 'movieDetail',
    component: routesComponent.movieDetailComponent
  },

  {
    path: '*',
    redirect: {name: 'index'}
  }

]
