import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import axios from 'axios'

import VueAxios from 'vue-axios'



import {
  Field,
  CellGroup,
  Button,
	Image,
  Uploader,
  Notify,
  Dialog,
  Tabbar,
  TabbarItem,
  Row,
  Col,
  Icon,
  Tabs,
  Tab,
  Loading,
  Swipe,
  SwipeItem,
  Lazyload,
  Toast,
  Skeleton,
  NavBar,
  Rate,
  Area,
  Overlay,
  Grid,
  GridItem,
  Cell,
} from 'vant'



import 'lib-flexible/flexible'

Vue.use(VueAxios, axios)

Vue
  .use(Field)
  .use(CellGroup)
  .use(Button)
  .use(Image)
  .use(Uploader)
  .use(Notify)
  .use(Dialog)
  .use(Tabbar)
  .use(TabbarItem)
  .use(Row)
  .use(Col)
  .use(Icon)
  .use(Tabs)
  .use(Tab)
  .use(Loading)
  .use(Swipe)
  .use(SwipeItem)
  .use(Lazyload)
  .use(Toast)
  .use(Skeleton)
  .use(NavBar)
  .use(Rate)
  .use(Area)
  .use(Overlay)
  .use(Grid)
  .use(GridItem)
  .use(Cell)

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
