
// 全局获取本地存储方法
window.getjsonloca = function(text, type) {
    let shuju = localStorage.getItem(text);
    return shuju = shuju ? JSON.parse(shuju) : type;
}

// 全局更新/插入本地存储方法
window.insertjsonloca = function(text, obj){
  //JSON.stringify(data);把对象转为字符串
  localStorage.setItem(text, JSON.stringify(obj));
}

// 全局用户注册插入本地存储方法
window.insertuser = function(obj) {
  let shuju = getjsonloca('tableuser', {});
  if(Object.keys(shuju).length > 0 && shuju[obj.account] != undefined){
    return false;
  }else{
    shuju[obj.account] = obj;
    insertjsonloca('tableuser', shuju);
    return true;
  }
}

// 全局获取用户信息方法
window.getUserName = function(nameId) {
  let shuju = getjsonloca('tableuser', {});
  if(Object.keys(shuju).length > 0 && shuju[nameId] != undefined){
    return shuju[nameId];
  }else{
    return false;
  }
}

// 全局定义
// 节流函数--函数防抖
// antiShake(对象，触发事件，回调函数，间隔时间)
window.antiShake = function(obj,trigger, fn, shi) {
	var timers = [];
	obj[trigger] = function() {
		var tim = setTimeout(function() {
			for (var i = 1; i < timers.length; i++) {
				clearTimeout(timers[i]);
			}
			timers = [];
			return fn();
		}, shi)
		timers.push(tim);
  }
}

// 存储cookie方法(名, 值, 时间(小时))
window.tianjia = function(mingcheng,zhi,shijian){
	var d = new Date();
	d.setTime(d.getTime() + shijian * 60 * 60 * 1000);
	document.cookie = mingcheng + "=" + zhi + "; " + "expires=" + d.toGMTString();
}

// 读取cookie方法(名)
window.huoquzhi = function(name){
	var arr = document.cookie.split(";");
	for(var i in arr){
		var shuzhu = arr[i].split("=");
		if(shuzhu[0] == name){
			return shuzhu[1];
		}
	}
  return false;
}

